@import '../../sass/utils.scss';

.block {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.title {
  margin-bottom: 50px;
  text-align: center;
}
.list {
  margin-bottom: 50px;
  display: grid;
  gap: 20px;
  @include tablet {
    gap: 16px;
    grid-template-columns: repeat(2, 344px);
  }
  @include laptop {
    gap: 29px;
    grid-template-columns: repeat(3, 282px);
  }
}

.btnBlock {
  display: flex;
}
