@import '../../sass/utils.scss';
.container {
  max-width: 360px;
  margin: 0 auto;
  padding: 0 16px;
  @include tablet {
    max-width: 768px;
    padding: 0 32px;
  }
  @include laptop {
    max-width: 1024px;
    padding: 0 60px;
  }
  @include xl {
    max-width: 1170px;
    padding-left: 0;
    padding: 0;
  }
}
